/* CSS that will be loaded on the frontend of single posts (except Pages) */

@import 'variables';

%form-box{
    margin: 30px 0;
    border-radius: 20px;
    background: #fff;
    border: 1px solid rgba(140, 149, 170, 0.29);
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    padding: 35px;
}

.single-content-section{
    .container{
        max-width: calc( #{$container-single} + 100px );
    }
}

.single-content-section{
    --headingH1: 40px;
	--headingH2: 32px;
	--headingH3: 28px;
	--headingH4: 24px;
	--headingH5: 20px;
	--headingH6: 16px;
    --singlePostFontSize: 22px;
    line-height: 1.625;
    @media only screen and (min-width: 990px){
        font-size: var(--singlePostFontSize);
    }
    
    h1, h2, h3, h4, h5, h6,
    .h1, .h2, .h3, .h4, .h5, .h6{
        margin-top: 1.5em;
        margin-bottom: 1em;
    }
    h1, .h1{
        line-height: 1.375;
    }
    h2, .h2{
        --headingH2Color: var(--colorP1);
        line-height: 1.312;
        font-weight: var(--bold);
    }
    h3, .h3{
        --headingH3Color: var(--colorP1);
        font-weight: var(--bold);
    }
    h4, .h4{
    }
    h5, .h5{
    }
    h6, .h6{
    }

    ul{
        --listIconPositionV: 12px;
    }

    blockquote{
        font-size: 26px;
        line-height: 1.8;
        color: var(--colorP3);
        cite{
            font-weight: var(--medium);
            font-size: 22px;
            line-height: 1.63;
            color: var(--colorP1);

        }
    }

    .avia-iframe-wrap iframe{
        border-radius: 20px;
    }

    table{
        width: 100%;
        td{
            border: 1px solid;
            padding: 5px 15px;
        }
    }

    .single-post-footer,
    .single-post-header{
        & > *{
            margin: var(--spaceBetweenItemsHeader, 20px) 0;
        }
    }

    .single-post-meta{
        display: flex;
        font-size: 16px;
        color: var(--colorE11);
        a{
            --linkColor: var(--colorP1);
        }
        .ep-post-meta{
            @media only screen and (min-width: 768px){
                display: flex;
            }
            & > *:not(:last-child){
                margin-right: var(--spaceBetweenItems, 20px);
            }
        }
        .single-post-authors{
            display: flex;
            align-items: center;
            &:after{
                content: '';
                display: inline-block;
                width: 1px;
                height: 75%;
                background: var(--colorE11);
                margin: 0 10px;
            }
        }
    }

    .single-post-thumbnail{
        img{
            width: 100%;
            border-radius: 20px;
            aspect-ratio: var(--featuredImageAspectRatio, 777/432);
            object-fit: cover;
            object-position: center;
        }
        &.has-shadow{
            img{
                box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.16);
            }
        }
    }

}

.av-share-box{
    border-radius: 6px;
    background: rgba(20, 36, 78, 0.05);
    border: 1px solid var(--colorP3);
    padding: 20px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .av-share-link-description{
        margin: 0;
        font-size: 18px;
        color: var(--colorP3);
    }
    .av-share-box-list{
        display: flex;
        li{
            line-height: 1;
            &:not(:last-child){
                margin-right: 20px;
            }
            a{
                color: var(--colorP3);
                text-decoration: none;
                font-size: 22px;
                &:hover{
                    color: var(--colorP1);
                }
            }
        }
    }
}

.author-box{
    --headShotSize: 62px;
    margin-top: 60px;
    padding: 50px 40px;
    border-radius: 12px;
    background: var(--colorT4);
    font-size: 14px;
    h6{
        margin: 0 0 30px;
    }
    .author{
        display: flex;
        flex-wrap: wrap;
        &:not(:last-child){
            margin-bottom: 15px;
        }
    }
    .author-header{
        display: flex;
    }
    .author-img{
        width: var(--headShotSize);
        margin-right: 25px;
        img{
            display: block;
            height: var(--headShotSize);
            border-radius: 100%;
            background: var(--colorP3);
            border: 2px solid var(--colorP2);
        }
        & + .author-content{
            width: calc(100% - var(--headShotSize) - 25px);
        }
    }
    .author-content > *{
        margin-bottom: 15px;
    }
    .author-name,
    .author-title{
        margin: 0;
        font-weight: var(--bold);
    }
    .author-name{
        font-size: 32px;
    }
    .author-socials{
        a{
            &:before{
                content: "";
                font-size: 20px;
                font-family: serif;
            }
        }
    }
}

.related-posts-section{
    border-top: 1px solid var(--colorT1);
    .related-posts-header{
        --epColumnWidth: auto;
        .ep-columns-inner{
            justify-content: space-between;
        }
    }
}


/* template specific */
.single-post-columnized{
    --singlePostFontSize: 16px;
    --spaceBetweenItems: 10px;
    --featuredImageAspectRatio: 600 / 332;
    --spaceBetweenItemsHeader: 10px;
    ul{
        --listIcon: "\e818";
        --listIconFontColor: var(--colorE7);
        --listIconColor: transparent;
        --listIconPositionV: 0;
        --listIconSize: 18px;
        --dotSize: auto;
        --listIconPositionV: 4px;
        --listInnerGap: 0 0 0 1.6em;
    }
    .container{
        max-width: 100% !important;
        width: 100% !important;
    }
    .entry-content-wrapper{
        @media only screen and (max-width: 1265px){
            padding-left: 50px;
            padding-right: 50px;
        }
    }
    .ep-columns{
        max-width: 1210px !important;
    }
    .single-post-main-content{
        flex: 1;
        &.is-resource{
            .hbspt-form{
                @extend %form-box;
            }
        }
        .single-post-header{
            .ep-post-terms{
                margin-top: 0;
            }
        }
    }
    .single-post-side-content{
        --epColumnWidth: 470px;
        @media only screen and (max-width: 989px){
            --epColumnWidth: 100%;
        }
        &.is-resource{
            --epColumnWidth: 311px;
            --featuredImageAspectRatio: default;
            img{
                border-radius: 6px;
                box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.16);
            }
        }
        h6{
            margin-bottom: 0;
        }
    }
}

.form-box{
    @extend %form-box;
}
.single-post-footer-desktop{
    display: none;
    @media only screen and (min-width: 999px){
        display: block;
    }
}

.single-post-footer-mobile{
    @media only screen and (min-width: 999px){
        display: none;
    }
}

// Legacy Hubspot forms within posts solution
.single-content-section .entry-content-wrapper p > .hbspt-form,
.single-content-section .entry-content-wrapper .post-form .hbspt-form {
	background-color:#14244E;
	border-radius: 20px;
	padding: 55px 25px;
	filter: drop-shadow(0 3px 6px rgba(0,0,0,0.16));
}
